.container {
  height: 300px;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  background-color: #f0f2f5;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

h1 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
}

.display {
  font-size: 4rem;
  color: #4a90e2;
  margin: 20px 0;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.button-group {
  display: flex;
  gap: 10px;
}

.button {
  padding: 10px 20px;
  font-size: 1.5rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.increment {
  background-color: #4caf56;
  color: #fff;
}

.decrement {
  background-color: #f44336;
  color: #fff;
}

.reset {
  background-color: #ff9800;
  color: #fff;
}

.button:hover {
  opacity: 0.9;
}
